.container {
  width: 300px;
  margin: 0 auto;
}

#search-box {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

#country-info {
  font-family: Arial, sans-serif;
}

